import React from 'react';
import { Descriptions } from 'antd';
import { observer } from 'mobx-react';
// 
@observer
class Parameter extends React.Component {    
    render() {
        const { oneSpecs } = this.props;
        return (
            <div className='Parameter'>
                <Descriptions title="">
                    <Descriptions.Item label="">{ oneSpecs[0].lname }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].os }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].memory }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].resolution }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].video_card }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].cpu }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].video_memory }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].category }</Descriptions.Item>
                    <Descriptions.Item label="">{ oneSpecs[0].disk }</Descriptions.Item>
                </Descriptions>
            </div>
        );
    }
}

export default Parameter;