import service from './service';
import { message } from 'antd';

class State {
    //  - 
    updateUserInfoData = async (values = {}) => {
        const res = await service.updateUserInfoData(values);
        try{
            if( res.data.code === 200 ){
                message.success('!')
            }
        }catch(err) {
            console.log(err);
        }
    }
}

export default new State();