import React, { Fragment } from 'react';
import { Row, Col, Icon } from 'antd';
import { observer } from 'mobx-react';
// 
import state from './state';
// 
import $state from '@store';
// less
import './index.less';

// 
@observer
class TopMenu extends React.Component {

    // 
    intoTargetPage = (that) => {
        if( that == 'login' ){
            this.props.history.push('/login');
        }else if( that == 'register' ){
            this.props.history.push('/register');
        }else if( that == 'order' ){
            this.props.history.push('/views/products/order');
        }else if( that == 'collection' ){
            this.props.history.push('/views/products/collection');
        }else if( that == 'userCenter' ){
            this.props.history.push('/views/user');
        }else if( that == 'logout' ){
            state.logoutData();
        }else if( that == 'admin' ){
            this.props.history.push('/admin');
        }
    }

    render() {
        const { uname } = $state;
        return (
            <div className='dm_topMenu'>
                <Row className='common_width'>
                    <Col span={ 6 }>
                        <Icon type="environment" style={{ paddingRight: '4px' }} />
                        
                    </Col>
                    <Col span={ 18 }>
                        {
                            uname ? (
                                <Fragment>
                                    <span>{ uname }</span>
                                    <span onClick={ this.intoTargetPage.bind(this, 'logout') }></span>
                                </Fragment>
                            ) : (
                                <Fragment>
                                    <span onClick={ this.intoTargetPage.bind(this, 'login') }></span>
                                    <span onClick={ this.intoTargetPage.bind(this, 'register') }></span>
                                </Fragment>
                            )
                        }
                        <span onClick={ this.intoTargetPage.bind(this, 'order') }></span>
                        <span onClick={ this.intoTargetPage.bind(this, 'collection') }></span>
                        <span onClick={ this.intoTargetPage.bind(this, 'userCenter') }></span>
                        <span onClick={ this.intoTargetPage.bind(this, 'admin') }>Demo_Mall </span>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default TopMenu;