import React from 'react';
import { observer } from 'mobx-react';
import { Table, Typography, Row, Col, message, Button } from 'antd';

// 
import { columns } from './data';
// 
import state from './state';
// less
import './index.less';

// 
@observer
class MyCollection extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedRowKeys: [],
            selectedRows: []
        }
    }

    componentDidMount() {
        state.selcolsListData();
    }

    // 
    rowSelection = {
        onChange: (selectedRowKeys, selectedRows) => {
            this.setState(() => ({
                selectedRowKeys,
                selectedRows
            }))
        }
    };

    // 
    footer = () => {
        return (
            <Row>
                <Col span={ 12 } className='left'>
                    <Button onClick={ this.handleCancelCollection }></Button>
                    <Button onClick={ this.handleCollectionProduct }></Button>
                </Col>
                <Col span={ 12 } className='right'>
                    <span className='num'><i>{ this.state.selectedRowKeys.length }</i></span>
                </Col>
            </Row>
        );
    }

    // 
    handleCancelCollection = () => {
        const { selectedRowKeys } = this.state;
        if( selectedRowKeys.length ){
            state.delcolsData( selectedRowKeys );
        }else{
            message.warning('');
        }
    }

    // 
    handleCollectionProduct = () => {
        const { selectedRowKeys, selectedRows } = this.state;
        if( selectedRowKeys.length && selectedRows.length ){
            state.addcartData( selectedRowKeys, selectedRows );
        }else{
            message.warning('');
        }
    }

    render() {
        const { dataSource } = state;
        return (
            <div className='common_width dm_MyCollection'>
                <Row className='table_title'>
                    <Typography.Title level={ 4 }></Typography.Title>
                    <div> <i>{ dataSource.length }</i> </div>
                </Row>
                <Table 
                    rowSelection={ this.rowSelection } 
                    columns={ columns } 
                    dataSource={ dataSource } 
                    rowKey={ (record) => record.id }
                    pagination={ false }
                    scroll={{ x: false, y: false }}
                    footer={ this.footer }
                    bordered
                />
            </div>
        );
    }
}

export default MyCollection;