import React from 'react';
import { PUBLIC_URL } from '@config';
import { observable } from 'mobx';

class Data {

    // 
    @observable columns01 = [
        {
            title: '',
            dataIndex: 'consignee',
            key: 'consignee'
        },
        {
            title: '',
            dataIndex: 'region',
            key: 'region'
        },
        {
            title: '',
            dataIndex: 'addressDetails',
            key: 'addressDetails'
        },
        {
            title: '',
            dataIndex: 'phone',
            key: 'phone'
        }
    ];

    // 
    @observable columns02 = [
        {
            title: '',
            dataIndex: 'imgs',
            key: 'imgs',
            align: 'center',
            width: '10%',
            render: (text, record, index) => <img className='imgs-style' src={ `${ PUBLIC_URL }${ text }` } alt={ text } />
        },
        {
            title: '',
            dataIndex: 'title',
            key: 'title',
            width: '42%',
            render: (text, record, index) => {
            return (
                <div className='title-style'>
                <p>{ text }</p>
                <span>{ record.spec }</span>
                </div>
            );
            }
        },
        {
            title: '',
            dataIndex: 'price',
            key: 'price',
            align: 'center',
            width: '10%',
            render: (text, record, index) => text ? `${parseFloat( text ).toFixed(2)}` : 0
        },
        {
            title: '',
            dataIndex: 'pNum',
            key: 'pNum',
            align: 'center',
            width: '6%',
            render: (text, record, index) => `x ${text}`
        },
        {
            title: '',
            dataIndex: 'operation',
            key: 'operation',
            align: 'center',
            width: '16%',
            render: (text, record, index) => ''
        }
    ];

}

export default new Data();